@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
}

.main-container {
  width: 100%;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

nav {
  color: white;
  background-color: dodgerblue;
  width: 100%;
  height: 75px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.nav-links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 500px;
}

.home-container {
  height: 100%;
}

.catalog-container {
  height: 100%;
  display: -webkit-flex;
  display: flex;
}

aside {
  display: -webkit-flex;
  display: flex;
  width: 15%;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.products-container {
  width: 100%;
  margin: 2rem;
  display: grid;
  grid-gap: 1.5rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.cart-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.cart-wrapper {
  width: 80%;
  margin: 1rem;
  display: grid;
  grid-gap: 1.5rem;
  grid-template-rows: repeat(auto-fit, minmax(250px, 250px));
}

.product-card-wrapper {
  display: -webkit-flex;
  display: flex;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
}

.quantity-form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.btn-change-quantity {
  width: 42px;
  height: 42px;
  outline: none;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 2rem;
}

.quantity-form > input {
  width: 64px;
  height: 42px;
  line-height: 42px;
  text-align: center;
  border: 1px solid #e6dcd2;
  background: none;
  font-size: 1.5rem;
  font-family: 'Montserrat SemiBold';
  font-weight: 600;
}

.main-section {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.main-section > img {
  height: 200px;
}

.product-card {
  width: 300px;
  height: 400px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.image-wrapper {
  width: 200px;
  height: 250px;
  display: -webkit-flex;
  display: flex;
}

.image-wrapper > img {
  max-width: 200px;
  max-height: 250px;
  margin: auto;
  display: block;
  cursor: pointer;
}

.product-card > h4 {
  text-align: center;
  width: 90%;
}

.categories-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  list-style: none;
  -webkit-align-items: center;
          align-items: center;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  padding-top: 1rem;
}

.categories-container > li {
  font-size: 1.2rem;
  cursor: pointer;
}

.product-details-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.add-btn {
  border-radius: 5px;
  font-size: 1.2rem;
  padding: 0;
  width: 200px;
  height: 2.5rem;
  background-color: rgb(215, 255, 189);
}

